}

.clossBox{
    position:fixed;
    top:0;
    left:0;
    width:100vw;
    height:100vh;
    background-color:rgba(0,0,0,0);
    z-index: 10;
}

.modalBox{
    padding:20px 40px;
    background-color:#fff;
    min-width:364px;
    border-radius: 56px;
    position:fixed;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    box-shadow:0 0 5px 8px rgba(0,0,0,.1);
    z-index:200;
}

.modal .clossBox{
    background-color:rgba(0,0,0,.2);
}
#header{
    width:100%;
