}

#header:hover .talk_to_sales{
    background-color:#000;
}

#header:hover .talk_to_sales a{
    color:#fff;
}


.navListBox{
    background-color:#fff;
    box-shadow: 3px 4px 10px rgba(0, 0, 0, 0.15);
    border-radius: 0px 0px 40px 40px;
    position:absolute;
    top:40px;
    left:0;
    width:100%;
    padding:10px;
    z-index: 10;
}

.navListBox a{
    display:block;
    font-family: Spartan;
    font-weight: 500;
    font-size: 12px;
    line-height: 40px;
    text-align: center;
    color: #B7B7B7;
    cursor: pointer;
}

.navListBox a.on{
    color: #02BDC8;
}

.header_mobile{display:none}

@media only screen and (max-width: 1350px){
