.datasets_wrap{
    width:100%;
}

.datasets{
    padding:0 80px;
}

.h2{
    font-weight: 300;
    font-size: 45px;
    line-height: 69px;
    text-align: center;
    letter-spacing: -0.01em;
    color: #000000;
    margin:150px 0 30px;
}

.datasets_desc{
    font-size: 15px;
    line-height: 25px;
    text-align: center;
    letter-spacing: -0.01em;
    color: #B7B7B7;
    margin-bottom:65px;
}

.lists{
    display:grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap:90px 56px;
    gap:90px 56px;
    padding-bottom:150px
} 

.list{
    background: #FFFFFF;
    box-shadow: 0px 4px 38px rgba(0, 0, 0, 0.1);
    border-radius: 60px;
    overflow: hidden;
    transition:all 0.2s;
    position:relative;
    top:0;
}

.list:hover{
    top:-10px;
}

.datasets_wrap .img{
    position: relative;
    padding-bottom:100%;
    border-radius: 60px;
    overflow: hidden;
}

.datasets_wrap .img img{
    display:block;
    position:absolute;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.list_text{
    padding:28px 36px 22px;
}

.list_title{
    font-family: Spartan;
    font-weight: bold;
    font-size: 24px;
    line-height: 27px;
    color: #280E0E;
